@viewport {
    orientation: portrait;
}

@media screen and (orientation: landscape) {
    html {
        /* Rotate the content container */
        transform: rotate(-0deg);
    }
}

:root {
    --color-light-shades: #E13B64;
    --color-light-accent: #A30036;
    --color-primary: #047AED;
    --color-secondary: #e6b8afff;
    --color-dark-accent: #A7AABD;
    --color-dark-shades: #424656;
    --color-danger: #ff0000;
    --color-info: #00BCD4;
    --ff-primary: 'Roboto', sans-serif;
    --ff-accent: 'Playfair Display', serif;
}

*,
*::before,
*::after {
    box-sizing: border-box;
}

body {
    min-height: 100vh;
    /* mobile viewport bug fix */
    min-height: -webkit-fill-available;
}

html {
    height: -webkit-fill-available;
}

body,
h1,
h2,
h3,
p {
    margin: 0;
}

body {
    font-family: var(--ff-primary);
    font-weight: 400;
    font-size: 1.3125rem;
    line-height: 1.6;
    position: fixed;
    width: 100%;
}

@media screen and (max-height: 500px) {
    body {
        font-size: 1rem;
    }
}

a,
a:visited {
    color: var(--color-primary);
}

h1,
h2,
h3 {
    font-family: var(--ff-accent);
    font-weight: 900;
    line-height: 1;
}

h2,
h3,
p {
    margin-bottom: 0.2em;
}

.logo-container {
    margin: auto;
    max-width: 40vh;
}

img {
    display: block;
    width: 100%;
    height: auto;
    margin: auto;
}

