    max-width: 450px;
    overflow: auto;
    margin: 0;
    background: white;
    padding: 1em;
    border: 1px solid red;
}

header {
    padding: 0.5rem 0;
}

.container {
    /* margin-inline: auto;
    width: min(90%, 70.5rem);
    max-width: 450px; */
    max-width: 450px;
    margin: 1em;
    background: white;
}

.flex {
    display: flex;
}

.flex-center {
    display: flex;
    align-items: center;
    justify-content: center;
}

.flex-center-start {
    display: flex;
    align-items: center;
    justify-content: start;
}

.flex-d-row {
    flex-direction: row;
}

.flex-d-column {
    flex-direction: column;
}

.flex-a-start {
    align-items: start;
}

.flex-a-center {
    align-items: center;
}

.flex-j-start {
    justify-content: start;
}

.flex-j-center {
    justify-content: center;
}

.m-1 {
    margin: 1em;
}

.m-2 {
    margin: 2em;
}

.m-3 {
    margin: 3em;
}

.m-4 {
    margin: 4em;
}

.p-1 {
    padding: 1em;
}

.p-2 {
    padding: 2em;
}

.p-3 {
    padding: 3em;
}

.p-4 {
    padding: 4em;
}

.fsize-1 {
    font-size: 1em;
}

.fsize-2 {
    font-size: 2em;
}

.fsize-3 {
    font-size: 3em;
}

.fsize-3 {
    font-size: 4em;
}

.text-center {
    text-align: center;
}

.text-right {
    text-align: right;
}

.text-left {
    text-align: left;
}

.align-placeholder {
    position: relative;
}

.align-right {
    position: absolute;
    left: auto;
    right: 1em;
}

.align-left {
    position: absolute;
    left: 0;
    right: auto;
}

.navbar-top {
    position: fixed;
    padding: 0;
    margin: auto;
    max-width: 450px;
    width: 100%;
    top: 1em;
    left: 0;
}

.navbar-profile-top {
    position: fixed;
    padding: 0.5rem 0;
    margin: auto;
    max-width: 450px;
    width: 100%;
    top: 1em;
    left: 0;
}

.navbar-placeholder {
    position: relative;
    margin-top: 6em;
}

.main-container {
    /* max-height: 55vh; */
    overflow: auto;
    max-height: calc(80vh - 5em);
}

::-webkit-scrollbar {
    width: 5px;
}


/* Track */

::-webkit-scrollbar-track {
    background: #f1f1f1;
}


/* Handle */

 ::-webkit-scrollbar-thumb {
    background: #888;
}


/* Handle on hover */

 ::-webkit-scrollbar-thumb:hover {
    background: #555;
}

.navbar-bottom {
    position: fixed;
    display: flex;
    padding: 0;
    margin: auto;
    max-width: 450px;
    width: 100%;
    bottom: 0em;
    left: 0;
    border: 1px solid black;
    background-color: white;
}

.navbar-bottom a:nth-of-type(1) {
    margin-left: auto;
}

.navbar-bottom a:nth-of-type(2) {
    margin-left: auto;
    position: relative;
    top: -1.0em;
    background-color: #ffffff;
    border-radius: 50%;
    border: -1px solid white;
}

.navbar-bottom a:nth-of-type(3) {
    margin-left: auto;
    margin-right: auto;
}

.clearFloar::after {
    content: "";
    clear: both;
}

.signin___button {
    cursor: pointer;
    outline-style: none;
    color: white;
    border: 1px solid var(--color-primary);
    border-radius: 3em;
    background-color: var(--color-primary);
    padding: 0.8em 0;
}

.history___button {
    cursor: pointer;
    outline-style: none;
    color: white;
    border: 1px solid var(--color-info);
    border-radius: 3em;
    background-color: var(--color-info);
    padding: 0.8em 0;
    width: 100%;
}

.delete___button {
    cursor: pointer;
    outline-style: none;
    color: white;
    border: 1px solid var(--color-danger);
    border-radius: 3em;
    background-color: var(--color-danger);
    padding: 0.8em 0;
    width: 100%;
}

.border {
    border: 1px solid black;
    border-radius: 0.2em;
    padding: 0.2em;
}

.separator {
    max-height: 3px;
}

.card {
    margin-left: 0em;
    display: flex;
    flex-direction: column;
}

.card input[type="email"],
.card input[type="password"],
.card button[type="submit"] {
    width: 100%;
}

.card input[type="email"]:disabled,
.card button[type="submit"]:disabled,
button:disabled {
    background: rgb(112, 112, 112);
}

.projects {
    display: grid;
    grid-template-columns: repeat( auto-fit, minmax(6em, 2fr));
}

.project {
    border: 1px solid black;
    border-radius: 0.2em;
    margin: 0.2em;
    cursor: pointer;
    min-height: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.project input[type="text"] {
    width: 80%;
    margin: 0 1em;
}

.createProject input[type="text"],
.createProject input[type="color"],
.createProject button[type="submit"],
.createProject button[type="submit"]:disabled {
    width: 100%;
    margin: 0 1em;
}

.task-list {
    display: flex;
    border-bottom: 1px solid black;
    margin-bottom: 0.2em;
}

.task-listing {
    overflow: auto;
    max-height: calc(84vh - 5em);
    width: 100%;
    margin: 0;
    padding: 0;
}

.task-list li {
    text-decoration: none;
    list-style-type: none;
}

.task-list p {
    margin-left: 0.2em;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

.history-list {
    overflow: auto;
    max-height: calc(90vh - 5em);
    width: 100%;
    margin: 0;
    padding: 0;
}

.history-list li {
    text-decoration: none;
    list-style-type: none;
    font-size: 0.7em;
    padding: 1em;
}

.history-list li>div>div {
    margin-right: 1em;
}

.repeat-modal {
    position: absolute;
    background-color: rgba(0, 0, 0, 0.8);
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 10;
}

.repeat-content {
    z-index: 100;
    background-color: white;
    border-radius: 8px;
}

.repeat-content input[type="number"] {
    width: 20%;
}

.repeat-background-modal {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: transparent;
    z-index: 50;
}